.dayCard {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 8.57rem;
  height: 12.64rem;
  background-color: $bg-1;
  color: $light;
  font-family: $font-1;
  font-size: 1.14rem;
  font-weight: 500;
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
  -ms-border-radius: 0.5rem;
  -o-border-radius: 0.5rem;

  &__weather {
    width: 50%;
  }

  &__temp {
    width: 90%;
    display: flex;
    justify-content: space-evenly;

    & .min {
      color: $grey-3;
    }
  }
}
