.searchPanel {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: $bg-1;
  padding: 1.29rem 0.86rem;
  z-index: 1000;

  &__close {
    width: 100%;
    display: flex;
    line-height: 0;
    justify-content: flex-end;
    margin-bottom: 2rem;

    svg {
      width: 1.71rem;
      height: 1.71rem;
      color: $light;
      cursor: pointer;
    }
  }

  &__search {
    display: flex;
    position: relative;
    color: $grey-4;
    line-height: 0;
    gap: 1rem;

    svg {
      width: 1.43rem;
      height: 1.43rem;
      position: absolute;
      top: 50%;
      left: 1.21rem;
      transform: translateY(-50%);
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      -o-transform: translateY(-50%);
    }

    &__input {
      color: $light;
      font-family: $font-1;
      font-size: 1.14rem;
      font-weight: 500;
      padding: 1rem 1rem 1rem 3.5rem;
      background-color: $bg-1;
      border: 1px solid $light;
      border-radius: 0.21rem;
      -webkit-border-radius: 0.21rem;
      -moz-border-radius: 0.21rem;
      -ms-border-radius: 0.21rem;
      -o-border-radius: 0.21rem;

      &::placeholder {
        color: $grey-4;
        font-family: $font-1;
        font-size: 1.14rem;
        font-weight: 500;
      }
    }

    &__btn {
      padding: 1rem 1rem 1rem 1.14rem;
      background-color: $blue;
      color: $light;
      font-family: $font-1;
      font-size: 1.14rem;
      font-weight: 600;
      border: 1px solid $blue;
      border-radius: 0.21rem;
      -webkit-border-radius: 0.21rem;
      -moz-border-radius: 0.21rem;
      -ms-border-radius: 0.21rem;
      -o-border-radius: 0.21rem;
      transition: 0.3s;
      -webkit-transition: 0.3s;
      -moz-transition: 0.3s;
      -ms-transition: 0.3s;
      -o-transition: 0.3s;

      &:hover {
        opacity: 0.8;
      }
    }
  }
}

@media screen and (min-width: 600px) {
  .searchPanel {
    position: unset;
    min-width: 19rem;
    flex: 1 1;
    max-width: 32.79rem;
    min-height: 100vh;
    height: auto;
  }
}
