.params {
  padding: 0 1.71rem 3.64rem;
  background-color: $bg-2;
  color: $light;
  font-family: $font-1;

  &__container {
    max-width: 23.43rem;
    margin: auto;
  }

  h2 {
    font-weight: 700;
    font-size: 1.71rem;
    margin-bottom: 2.29rem;
  }

  &__cards {
    display: flex;
    flex-direction: column;
    gap: 2.29rem;

    &__card {
      max-width: 23.43rem;
      max-height: 14.57rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 1.57rem;
      background-color: $bg-1;
      border-radius: 0.5rem;
      -webkit-border-radius: 0.5rem;
      -moz-border-radius: 0.5rem;
      -ms-border-radius: 0.5rem;
      -o-border-radius: 0.5rem;

      h3 {
        font-weight: 500;
      }

      p {
        font-size: 2.57rem;
        font-weight: 500;
        margin-bottom: 1rem;
        text-align: center;

        span,
        label {
          font-size: 4.57rem;
          font-weight: 700;
        }
      }
      .wind_orientation {
        width: 30%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin: 0.64rem 0 0.63rem;

        span {
          font-size: 1rem;
          font-weight: 500;
        }
        .direction {
          line-height: 0;

          svg {
            width: 1.79rem;
            height: 1.79rem;
          }
        }
      }

      &.humidity {
        #humidity {
          width: 66%;
        }
        .before {
          width: 66%;
          font-size: 0.86rem;
          color: $grey-3;
          display: flex;
          justify-content: space-between;
        }
        .after {
          width: 66%;
          font-size: 0.86rem;
          color: $grey-3;
          display: flex;
          justify-content: flex-end;
        }
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .params {
    &__container {
      max-width: 50.29rem;
      margin: auto;
    }
    &__cards {
      flex-direction: row;
      flex-wrap: wrap;
      gap: 2.5rem;

      &__card {
        flex: 1 0 23.43rem;
      }
    }
  }
}
@media screen and (min-width: 1124px) {
  .params {
    &__cards {
      gap: 3rem;
    }
  }
}
@media screen and (min-width: 1224px) {
  .params {
    &__cards {
      gap: 3.29rem;
    }
  }
}
