.locationPanel {
  padding: 1.29rem 0.86rem 0;
  background-color: $bg-1;

  // button Part //
  &__btn {
    display: flex;
    justify-content: space-between;

    &__container {
      // both Buttons //
      .btn {
        font-family: $font-1;
        font-weight: 500;
        font-size: 1.14rem;
        color: $light;
        background-color: $grey-1;
        padding: 0.86rem 1.29rem;
        border: none;
        box-shadow: $shadow;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        border-radius: 0.21rem;
        -webkit-border-radius: 0.21rem;
        -moz-border-radius: 0.21rem;
        -ms-border-radius: 0.21rem;
        -o-border-radius: 0.21rem;

        &:hover {
          opacity: 0.8;
        }

        // Button Localisation //
        &.my__location {
          padding: 0.57rem;
          border-radius: 50%;
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          -ms-border-radius: 50%;
          -o-border-radius: 50%;
          font-size: 2rem;
          line-height: 0;
        }
      }
    }
  }

  // Info Part //
  &__main {
    position: relative;
    min-height: 23.5rem;
    margin: 0 -0.86rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__illustration {
      width: 10.71rem;
      height: 12.43rem;
    }

    &__background {
      background-position: center;
      background-size: 42rem;
      background-repeat: no-repeat;
      opacity: 0.05;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
    }
  }

  // Content Part //
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $grey-3;
    font-family: $font-1;
    font-weight: 500;

    &__degree {
      font-size: 10.29rem;
      color: $light;
      margin-bottom: 1.71rem;
      .degree__type {
        font-size: 3.43rem;
        font-weight: 400;
        color: $grey-3;
      }
    }

    &__type {
      font-size: 2.57rem;
      font-weight: 600;
      margin-bottom: 3.43rem;
    }

    &__date {
      font-size: 1.29rem;
      font-weight: 500;
      color: $grey-2;
      margin-bottom: 2.36rem;
    }

    &__loc {
      font-size: 1.29rem;
      font-weight: 500;
      color: $grey-2;
      margin-bottom: 7.5rem;
      text-align: center;
      & svg {
        transform: translateY(0.21rem);
        -webkit-transform: translateY(0.21rem);
        -moz-transform: translateY(0.21rem);
        -ms-transform: translateY(0.21rem);
        -o-transform: translateY(0.21rem);
      }
    }
  }
}

// Media Queries //

@media screen and (min-width: 600px) {
  .locationPanel {
    min-width: 19rem;
    flex: 1 1;
    max-width: 32.79rem;
    min-height: 100vh;
  }
}

@media screen and (min-width: 1050px) {
  .locationPanel {
    padding: 2.5rem 2.5rem 0;

    &__main {
      margin: 0 -2.5rem;
    }
  }
}
@media screen and (min-width: 1150px) {
  .locationPanel {
    padding: 3.29rem 3.29rem 0;

    &__main {
      margin: 0 -3.29rem;
    }
  }
}
