/* FOOTER */
.footer {
  padding: 1.21rem 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.21rem;
  text-align: center;
  margin: 0 -2.19rem;
}

/* LINK */
.footer a,
.footer a span {
  text-decoration: none;
  color: #f2f2f2;
}

.footer a span {
  text-decoration: underline;
  font-weight: 700;
}
