/* RESET */
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Raleway:wght@400;500;600;700&display=swap");

$font-1: "Raleway", sans-serif;
$font-2: "Montserrat", sans-serif;

$bg-1: #1e213a;
$bg-2: #100e1d;
$light: #e7e7eb;
$grey-1: #6e707a;
$grey-2: #88869d;
$grey-3: #a09fb1;
$grey-4: #616475;
$yellow: #ffec65;
$blue: #3c47e9;

$borderRadius: 0.86rem;
$shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

html {
  font-size: 14px;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  border-radius: $borderRadius;
  -webkit-border-radius: $borderRadius;
  -moz-border-radius: $borderRadius;
  -ms-border-radius: $borderRadius;
  -o-border-radius: $borderRadius;
}

button {
  cursor: pointer;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
  cursor: pointer;
}
img {
  width: 100%;
  display: block;
}
textarea {
  resize: none;
}
input {
  outline: none;
  border: none;
  width: 100%;
  height: 100%;
  font-family: $font-1;
  font-weight: 400;
  font-size: 1rem;

  &::placeholder {
    font-family: $font-1;
    font-weight: 400;
    font-size: 1rem;
  }
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}
