.unitChanger {
  display: flex;
  justify-content: space-between;
  margin: 1.29rem 2.5rem 0;
  align-self: flex-end;

  input[type="radio"] {
    appearance: none;
    width: 0;
    height: 0;
  }

  input:checked + label {
    color: #110e3c;
    background-color: #e7e7eb;
    border: solid 1px #e7e7eb;
  }

  label {
    transition: all 0.4s;
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -ms-transition: all 0.4s;
    -o-transition: all 0.4s;
    width: 2.86rem;
    height: 2.86rem;
    line-height: 2.86rem;
    text-align: center;
    color: $light;
    font-family: $font-1;
    font-weight: 700;
    font-size: 1.29rem;
    margin-left: 1rem;

    cursor: pointer;
    z-index: 90;
    color: #e7e7eb;
    background-color: #585676;
    border: solid 1px #585676;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;

    &:hover {
      // color: #110e3c;
      // background-color: #e7e7eb;
      // border: solid 1px #e7e7eb;
      box-shadow: 0px 0px 13px 2px rgba(231, 231, 235, 0.61);
    }
  }
}

//@media screen < 600px
@media screen and (max-width: 600px) {
  .unitChanger {
    justify-content: flex-end;
  }
}

// Media Screen for Margin ajustement with comp :
// Location Panel //

@media screen and (min-width: 1050px) {
  .unitChanger {
    margin-top: 2.5rem;
  }
}
@media screen and (min-width: 1150px) {
  .unitChanger {
    margin-top: 3.29rem;
  }
}
