.App {
  min-height: 100vh;
  background-color: $bg-2;

  &__content {
    margin: 0 auto;
  }
}

@media screen and (min-width: 600px) {
  .App {
    &__container {
      display: flex;
      background-color: $bg-2;
    }

    &__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    }
  }
}
